import React from "react";
import QueueAnim from "rc-queue-anim";
import Banner from "../../assets/min/cdd519ba6168c6087451cb730f621a54.jpeg";
import Banner2 from "../../assets/min/7e4824c7cce8d25fe1427e806d6320b6.jpeg";
import { Col, Row } from "antd";
import MapsReplace from "../../assets/min/mapsreplace.jpg";
import { Helmet } from "react-helmet";

const About = () => {
  return (
    <React.Fragment>
      <Helmet>
        <title>About IKI | we-iots</title>
        <meta
          name="description"
          content="IKI vision is to be a one-stop end-end IoT solution provider & mission to connect the world through IoT ecosystem infrastructure, replace manual business processes, and bring the IoT solution for digital transformation."
        />
        {/* <meta name="keywords" content="Smart Meter & Sensor IOT" /> */}
      </Helmet>
      <QueueAnim
        key="a"
        style={{
          height: "720px",
          overflow: "hidden",
          maxHeight: "720px",
          position: "relative",
        }}
      >
        <div
          key="1"
          style={{
            backgroundImage: `url(${Banner2})`,
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            backgroundPosition: "center",
            zIndex: 1,
            width: "100%",
            height: "100%",
            position: "absolute",
          }}
        ></div>
        <div
          key="2"
          style={{
            background: "rgba(0,0,0, 0.5)",
            position: "absolute",
            width: "100%",
            height: "100%",
            zIndex: 2,
          }}
        ></div>

        <div
          key="3"
          style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            zIndex: 3,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <span style={{ textAlign: "center" }}>
            <h1 style={{ color: "#fff", fontWeight: "700", fontSize: "48px" }}>
              PT. IOT KREASI INDONESIA
            </h1>
            <h2 style={{ color: "#fff", fontSize: "18px", lineHeight: "24px" }}>
              An IoT provider company based in Indonesia. <br /> Focusing on IoT
              Solution in the era of 4.0 <br /> and build an IoT platform to
              connect all the communication <br /> and information to achieve
              customer satisfaction.
            </h2>
          </span>
        </div>
      </QueueAnim>
      <div className="about-us-content">
        <img src={Banner} alt="About IKI" />
        <Row>
          <Col
            xl={12}
            lg={12}
            md={12}
            sm={24}
            xs={24}
            style={{ textAlign: "left" }}
          >
            <h3>Our Vision</h3>
            <p>One Stop End-End IOT Solution Provider</p>
          </Col>
          <Col
            xl={12}
            lg={12}
            md={12}
            sm={24}
            xs={24}
            style={{ textAlign: "left" }}
          >
            <h3>Our Mission</h3>
            <p>
              Connecting the world through IoT ecosystem infrastructure. We
              replace manual business process and bring the IoT solution for
              Digital Transformation
            </p>
          </Col>
        </Row>
      </div>
      <div className="footer-black-wiots">
        <div
          key="1"
          style={{
            backgroundImage: `url(${MapsReplace})`,
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
            backgroundPosition: "center",
            zIndex: 1,
            width: "70%",
            height: "100%",
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
          }}
        ></div>
        <div
          key="2"
          style={{
            background: "rgba(0,0,0, 0.5)",
            position: "absolute",
            width: "70%",
            height: "100%",
            zIndex: 2,
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
          }}
        ></div>

        <div
          key="3"
          style={{
            position: "absolute",
            width: "70%",
            height: "100%",
            zIndex: 3,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
          }}
        >
          <span style={{ textAlign: "center" }}>
            <h1 className="text-bottom-black-wiots">
              One Stop End-End IoT <br /> Solution Provider
            </h1>
          </span>
        </div>
      </div>
      <div className="white-bottom-weiots"></div>
      <div className="black-bottom-weiots"></div>
    </React.Fragment>
  );
};

export default About;
